import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Table = styled.table`
    width: 100%;
    border: 1px solid #444444;
    border-collapse: collapse;
`;

const TableTr = styled.tr`
    border: 1px solid #444444;
`;

const TableTh = styled.th`
    border: 1px solid #444444;  
    background: #eeeeee;
    padding: 5px;
`;

const TableTd = styled.td`
    border: 1px solid #444444;
    padding: 5px;
`;

const BtnGrp = styled.div`
    text-align: right;
`;

const Button = {
    marginRight : "4px"
}
const Board = () => {
    return(
        <>
            <h2>Board</h2>
            <hr/>
            <div style={{width:"80%", margin:"0 auto"}}>
                <BtnGrp>
                    <Link to='/board/write'><button style={Button}></button></Link>
                    <button style={Button}></button>
                    <button style={Button}></button>
                </BtnGrp>
                <br/>
                <Table>
                    <TableTr>
                        <TableTh>No</TableTh>
                        <TableTh></TableTh>
                        <TableTh></TableTh>
                        <TableTh></TableTh>
                    </TableTr>
                    <TableTr>
                        <TableTd>1</TableTd>
                        <TableTd> 1</TableTd>
                        <TableTd></TableTd>
                        <TableTd>2021-07-18</TableTd>
                    </TableTr>
                    <TableTr>
                        <TableTd>2</TableTd>
                        <TableTd> 2</TableTd>
                        <TableTd></TableTd>
                        <TableTd>2021-07-18</TableTd>
                    </TableTr>
                </Table>
            </div>

        </>
    );
}

export default Board;